.front-page {
  width:100%;
  height: 90vh;
  background-image: url('https://assets.hongkiat.com/uploads/minimalist-dekstop-wallpapers/4k/original/14.jpg');
  background-size: cover;
  display: grid;
  place-items: center;
}

.main-card {
  color:white;
  width: 30%;
  padding: 15px;
  text-align: left;
  border-radius: 12px;
  font-size: xx-large;
}

.introduction {
  margin-left: 25rem;
}

.contact {
  background-color: darkslategrey;
  color: white;
  padding: 10px;
  margin-top: 25px;
}

.footer-content {
  padding-left: 10rem;
}